import React, { useState } from "react";
import dynamic from "next/dynamic";
import { useRouter } from "next/router";
import { useTranslations } from "next-intl";
import Box from "@mui/material/Box";
import Toolbar from "@mui/material/Toolbar";
import IconButton from "@mui/material/IconButton";
import Typography from "@mui/material/Typography";
import Menu from "@mui/material/Menu";

import Container from "@mui/material/Container";
import Button from "@mui/material/Button";
import MenuItem from "@mui/material/MenuItem";

const Flight = dynamic(() => import("@mui/icons-material/Flight"));
const Hotel = dynamic(() => import("@mui/icons-material/Hotel"));
const DirectionsCar = dynamic(
  () => import("@mui/icons-material/DirectionsCar")
);

import ArrowDropDown from "@mui/icons-material/ArrowDropDown";

function setCookie(cname: string, cvalue: string, exdays: number) {
  const d = new Date();
  d.setTime(d.getTime() + exdays * 24 * 60 * 60 * 1000);
  let expires = "expires=" + d.toUTCString();
  document.cookie = cname + "=" + cvalue + ";" + expires + ";path=/";
}

import { FlagByLocale } from "./flags";
import Link from "./Link";
import Paper from "@mui/material/Paper";
import { useGlobal } from "../context";
import { trackEvent } from "../utils";
import Divider from "@mui/material/Divider";

const pages = [
  "navbar-flights",
  // "navbar-hotels",
  // "navbar-cars",
  // "navbar-explore",
];
const iconStyle = { color: "inherit" };
const items = [
  {
    i18n: "navbar-flights",
    icon: ({ sx }: any = {}) => <Flight sx={{ ...iconStyle, ...sx }} />,
    link: "routes-flights",
  },
  {
    i18n: "navbar-hotels",
    icon: ({ sx }: any = {}) => <Hotel sx={{ ...iconStyle, ...sx }} />,
    externalLink:
      "https://tp.media/r?marker=56123&trs=15329&p=2076&u=https%3A%2F%2Fbooking.com",
  },
  {
    i18n: "navbar-cars",
    icon: ({ sx }: any = {}) => <DirectionsCar sx={{ ...iconStyle, ...sx }} />,
    externalLink:
      "https://tp.media/r?marker=56123&trs=15329&p=3814&u=https%3A%2F%2Frentalcars.com",
  },
  // {
  //   i18n: "navbar-explore",
  //   icon: ({ sx }: any = {}) => <Public sx={{ ...iconStyle, ...sx }} />,
  //   link: "routes-explore",
  // },
];

const IconForRoute = ({ pathname }: any) => {
  const t = useTranslations();

  switch (pathname) {
    case "/":
    case `/${t(items[0].i18n)}`:
      return items[0].icon();
    // case `/${t(items[1].link)}`:
    //   return items[1].icon();
    // case `/${t(items[2].link)}`:
    //   return items[2].icon();
    // case `/${t(items[3].link)}`:
    //   return items[3].icon();
    default:
      return items[0].icon();
  }
};

const ResponsiveAppBar = () => {
  const t = useTranslations();
  const { locale, pathname, replace, push, query } = useRouter();

  const { setCurrency, setCurrencyByLocale, currency, languages, currencies } =
    useGlobal();

  const [showFlags, setShowFlags] = useState(false);
  const [showsIcons, setShowIcons] = useState(false);

  const [anchorElNav, setAnchorElNav] = useState<null | HTMLElement>(null);
  const [anchorElUser, setAnchorElUser] = useState<null | HTMLElement>(null);
  const [anchorElCurrency, setAnchorElCurrency] =
    useState<null | HTMLElement>(null);

  const handleOpenNavMenu = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorElNav(event.currentTarget);
    setShowIcons(true);
    trackEvent({
      action: "handleOpenNavMenu",
    });
  };
  const handleOpenLanguageMenu = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorElUser(event.currentTarget);
    setShowFlags(true);
    trackEvent({
      action: "handleOpenLanguageMenu",
    });
  };
  const handleOpenCurrencyMenu = async (
    event: React.MouseEvent<HTMLElement>
  ) => {
    setAnchorElCurrency(event.currentTarget);
    trackEvent({
      action: "handleOpenCurrencyMenu",
    });
  };

  const handleCloseNavMenu = (link?: string) => {
    setAnchorElNav(null);
    trackEvent({
      action: "handleCloseNavMenu",
      label: link,
    });
  };

  const handleCloseLanguageMenu = () => {
    setAnchorElUser(null);
    trackEvent({
      action: "handleCloseLanguageMenu",
    });
  };

  const handleLangChange = async (lang: any) => {
    const newLocale = lang.languageId;
    handleCloseLanguageMenu();
    // fetch new lang
    const newTranslation = (await import(`../../messages/index/${newLocale}`))
      .default;
    // extract key based on route name e.g. [routes-hotels] same in trasnlattion
    const translationString = pathname?.replace("/[", "")?.replace("]", "");
    replace({ pathname, query }, newTranslation[translationString], {
      locale: newLocale,
    });
    setCookie("NEXT_LOCALE", newLocale, 365);

    // Auto set currrency from that country
    // TODO check for cookie existance and avoid if it set
    setCurrencyByLocale(locale);

    trackEvent({
      action: "handleLangChange",
      label: lang,
    });
  };

  const handleCloseCurrencyMenu = () => {
    setAnchorElCurrency(null);
  };

  const handleCurrrencyChange = (currency: any) => {
    handleCloseCurrencyMenu();
    setCurrency(currency);
    // TODO save currency to cookie
    // setCookie("NEXT_LOCALE", newLocale, 365);
    trackEvent({
      action: "handleCurrrencyChange",
      label: currency?.code,
    });
  };

  return (
    // <AppBar position="relative" color="transparent" sx={{ borderRadius: 0 }}>
    <Paper elevation={0}>
      <Container maxWidth="xl">
        <Toolbar disableGutters>
          <Typography
            variant="h6"
            noWrap
            component="div"
            sx={{ mx: 2, display: { xs: "none", md: "flex" } }}
          >
            <Link
              href="/"
              onClick={() =>
                trackEvent({
                  action: "link",
                  label: "Flightscanner",
                })
              }
              sx={{ color: "inherit", textDecoration: "none" }}
            >
              Flightscanner
            </Link>
          </Typography>

          <Box sx={{ flexGrow: 1, display: { xs: "flex", md: "none" } }}>
            <Button
              size="large"
              aria-label="account of current user"
              aria-controls="menu-appbar"
              aria-haspopup="true"
              onClick={handleOpenNavMenu}
              color="inherit"
            >
              <IconForRoute pathname={pathname} />
              <ArrowDropDown />
            </Button>
            <Menu
              id="menu-appbar"
              anchorEl={anchorElNav}
              anchorOrigin={{
                vertical: "bottom",
                horizontal: "left",
              }}
              keepMounted
              transformOrigin={{
                vertical: "top",
                horizontal: "left",
              }}
              open={Boolean(anchorElNav)}
              onClose={() => handleCloseNavMenu()}
              sx={{
                display: { xs: "block", md: "none" },
              }}
            >
              {showsIcons &&
                items.map(({ i18n, icon, link, externalLink }) => (
                  <MenuItem
                    key={i18n}
                    onClick={() => {
                      handleCloseNavMenu(link);
                      link ? push(`/${t(link)}`) : window.open(externalLink);
                    }}
                  >
                    {icon({ sx: { mr: 2 } })}
                    <Typography textAlign="center">{t(i18n)}</Typography>
                  </MenuItem>
                ))}
            </Menu>
          </Box>
          {/* <Typography
            variant="h6"
            noWrap
            component="div"
            sx={{ flexGrow: 1, display: { xs: "flex", md: "none" } }}
          >
            <Box sx={{ display: { xs: "none", sm: "block " } }}>
              Flightscanner
            </Box>
          </Typography> */}
          <Box sx={{ flexGrow: 1, display: { xs: "none", md: "flex" } }}>
            {pages.map((page) => (
              <Link
                key={page}
                sx={{ ml: 2, my: 2, display: "block" }}
                href={`/${t(page.replace("navbar", "routes"))}`}
                underline="hover"
                color="inherit"
                onClick={() => trackEvent({ action: "link", label: page })}
              >
                {t(page)}
              </Link>
            ))}
            <Link
              key={"navbar-hotels"}
              sx={{ ml: 2, my: 2, display: "block" }}
              href={`https://tp.media/r?marker=56123&trs=15329&p=2076&u=https%3A%2F%2Fbooking.com`}
              target="_blank"
              underline="hover"
              color="inherit"
              onClick={() =>
                trackEvent({ action: "link", label: "navbar-hotels" })
              }
            >
              {t("navbar-hotels")}
            </Link>
            <Link
              key={"navbar-cars"}
              sx={{ ml: 2, my: 2, display: "block" }}
              href={`https://tp.media/r?marker=56123&trs=15329&p=3814&u=https%3A%2F%2Frentalcars.com`}
              target="_blank"
              underline="hover"
              color="inherit"
              onClick={() =>
                trackEvent({ action: "link", label: "navbar-cars" })
              }
            >
              {t("navbar-cars")}
            </Link>
          </Box>

          <Box sx={{ flexGrow: 0, mr: 2 }}>
            <IconButton onClick={handleOpenLanguageMenu} sx={{ p: 1 }}>
              <FlagByLocale locale={locale} />
            </IconButton>
            <Menu
              sx={{ mt: "45px" }}
              id="menu-appbar"
              anchorEl={anchorElUser}
              anchorOrigin={{
                vertical: "top",
                horizontal: "right",
              }}
              keepMounted
              transformOrigin={{
                vertical: "top",
                horizontal: "right",
              }}
              open={Boolean(anchorElUser)}
              onClose={handleCloseLanguageMenu}
            >
              {languages.map((language: any) => (
                <MenuItem
                  key={language.id}
                  onClick={() => handleLangChange(language)}
                >
                  {showFlags && (
                    <FlagByLocale sx={{ mr: 2 }} locale={language.regionId} />
                  )}
                  <Typography textAlign="center">{language.name}</Typography>
                </MenuItem>
              ))}
            </Menu>
          </Box>

          <Box sx={{ flexGrow: 0 }}>
            <Button
              onClick={handleOpenCurrencyMenu}
              sx={{ p: 0 }}
              color="inherit"
            >
              <Typography variant="button" textAlign="center">
                {currency.code} ({currency.symbol})
              </Typography>
            </Button>
            {/* <Autocomplete
              options={currencies}
              renderInput={(params) => <TextField {...params} label="Movie" />}
            /> */}
            <Menu
              sx={{ mt: "45px" }}
              id="menu-appbar2"
              anchorEl={anchorElCurrency}
              anchorOrigin={{
                vertical: "top",
                horizontal: "right",
              }}
              keepMounted
              transformOrigin={{
                vertical: "top",
                horizontal: "right",
              }}
              open={Boolean(anchorElCurrency)}
              onClose={handleCloseCurrencyMenu}
            >
              {currencies?.map((singleCurrency: any, index: number) =>
                index === 10 ? (
                  <span key={10}>
                    <Divider key={`${singleCurrency.code}` + "-divider"} />
                    <MenuItem
                      key={singleCurrency.code}
                      onClick={() => handleCurrrencyChange(singleCurrency)}
                    >
                      <Typography textAlign="center">
                        {singleCurrency.code} ({singleCurrency.symbol})
                      </Typography>
                    </MenuItem>
                  </span>
                ) : (
                  <MenuItem
                    key={`${singleCurrency.code}-${index}`}
                    onClick={() => handleCurrrencyChange(singleCurrency)}
                  >
                    <Typography textAlign="center">
                      {singleCurrency.code} ({singleCurrency.symbol})
                    </Typography>
                  </MenuItem>
                )
              )}
            </Menu>
          </Box>
        </Toolbar>
      </Container>
    </Paper>
    // </AppBar>
  );
};
export default ResponsiveAppBar;
