declare global {
  interface Window {
    gtag: any;
  }
}

// <action>	string		The value that will appear as the event action in Google Analytics Event reports.
// <category>	string	"general"	The category of the event.
// <label>	string		The label of the event.
// <value>	number		A non-negative integer that will appear as the event value.

// Category can be type e.g. button/link
// Action can be the name of the fn or CTA
// Label can be the specific change

type trarckEvent = {
  action: string;
  category?: string;
  label?: string;
  value?: number;
};

export const trackEvent = ({
  action,
  category,
  label,
  value,
}: trarckEvent) => {
  window &&
    window.gtag &&
    window.gtag("event", action, {
      event_category: category,
      event_label: label,
      value: value,
    });
};
