export enum EFlightSort {
  BEST,
  CHEAP,
  QUICK,
}

export interface IFlightsFilters {
  stop: EStopsFilterType;
  price: number[];
  duration: number[];
  times: number[][][];
  airlines: string[];
  gates: string[];
}

export enum EStopsFilterType {
  ANY,
  NON_STOP,
  ONE_STOP,
  TWO_STOP,
}

interface IFlightSearchResultsProposalTerm {
  currency: string;
  flights_baggage: any;
  flights_handbags: any;
  price: number;
  unified_price: number;
  url: number;
}

export interface IFlightSearchResultsProposal {
  carriers: string[];
  flight_weight: number;
  is_direct: boolean;
  is_popular: boolean;
  max_stop_duration: number;
  max_stops: number;
  min_stop_duration: number;
  rating: number;
  rating_summary: any;
  segment: any;
  segment_durations: number[];
  segments_airports: any;
  segments_time: any;
  sign: string;
  stops_airports: string[];
  terms: IFlightSearchResultsProposalTerms;
  total_duration: number;
  validating_carrier: string;
  xterms: any;
}

interface IFlightSearchResultsProposalTerms {
  [key: string]: IFlightSearchResultsProposalTerm;
}

const minBy = require("lodash.minby");
const maxBy = require("lodash.maxby");

/**
 * Sorting
 */
export interface ISortedFlights {
  [key: number]: any;
}

const sortNumbers = (a: any, b: any) => a - b;

export const sortFlightTerms = (terms: IFlightSearchResultsProposalTerms) => {
  return Object.values(terms).sort((flightA, flightB) => {
    return flightA.unified_price - flightB.unified_price;
  });
};

export const sortFlights = (
  flights: IFlightSearchResultsProposal[]
): ISortedFlights => {
  const cheapFlights = flights.slice().sort((flightA, flightB) => {
    const sortedTermsA = sortFlightTerms(flightA.terms);
    const sortedTermsB = sortFlightTerms(flightB.terms);
    return sortNumbers(
      sortedTermsA[sortedTermsA.length - 1].unified_price,
      sortedTermsB[sortedTermsB.length - 1].unified_price
    );
  });
  const quickFlights = flights.slice().sort((flightA, flightB) => {
    return sortNumbers(flightA.total_duration, flightB.total_duration);
  });
  const bestFlights = assignTempTicketRatingAndSort(
    flights.slice(),
    cheapFlights,
    quickFlights
  );

  return {
    [EFlightSort.BEST]: bestFlights,
    [EFlightSort.QUICK]: quickFlights,
    [EFlightSort.CHEAP]: cheapFlights,
  };
};

// Give every ticket a rating based on total duration + duration of flight
// Which basically gives it average of duration and cheapest
// So you avoid very cheap tickets but crazy huge durations
export const assignTempTicketRatingAndSort = (
  flights: IFlightSearchResultsProposal[],
  cheapFlights: IFlightSearchResultsProposal[],
  quickFlights: IFlightSearchResultsProposal[]
) => {
  return flights.sort((flightA, flightB) => {
    const sortedTermsA = sortFlightTerms(flightA.terms);
    const sortedTermsB = sortFlightTerms(flightB.terms);

    const cheapFlightsSortedTerms = sortFlightTerms(cheapFlights[0].terms);

    return sortNumbers(
      (flightA.total_duration / quickFlights[0].total_duration) *
        (sortedTermsA[0].unified_price /
          cheapFlightsSortedTerms[0].unified_price),
      (flightB.total_duration / quickFlights[0].total_duration) *
        (sortedTermsB[0].unified_price /
          cheapFlightsSortedTerms[0].unified_price)
    );
  });
};

/**
 * Filter flights
 * @param flights
 * @param filters
 */
export const filterFlights = (
  flights: IFlightSearchResultsProposal[],
  filters: IFlightsFilters
) => {
  // TODO if filters object is same as default dont filter - performance
  return flights.slice().filter((flight: IFlightSearchResultsProposal) => {
    // If we have 0 proposals ignore it for now
    const hasTerms = Object.keys(flight.terms).length > 0;

    if (!hasTerms) {
      return false;
    }

    if (filters.stop !== EStopsFilterType.ANY) {
      const isStopFine = filterFlightsStop(flight, Number(filters.stop));

      if (!isStopFine) {
        return false;
      }
    }

    if (filters.duration[0] !== 0 || filters.duration[1] !== 0) {
      // TODO if duration is part of min or max ignore filter or default
      const isDurationFine = filterFlightsDuration(
        flight,
        filters.duration[0],
        filters.duration[1]
      );

      if (!isDurationFine) {
        return false;
      }
    }

    if (filters.price[0] !== 0 || filters.price[1] !== 0) {
      // TODO if price is equal to min or max ignore filter ordefault
      const isPriceFine = filterFlightsPrice(
        flight,
        filters.price[0],
        filters.price[1]
      );

      if (!isPriceFine) {
        return false;
      }
    }

    // // TODO if times are equal to min or max ignore filter
    // const isTimesFine = filterFlightsTime(flight, filters.times);

    // if (!isTimesFine) {
    //   return false;
    // }

    // // TODO if airlines length equals to initial we allow all?
    // const isAirlineFine = filterFlightsAirline(flight, filters.airlines);

    // if (!isAirlineFine) {
    //   return false;
    // }
    // // TODO if gates length equals to initial we allow all?
    // const isGatesFine = filterFlightsGates(flight, filters.gates);

    // if (!isGatesFine) {
    //   return false;
    // }

    return true;
  });
};

/**
 *
 * @param flight
 * @param stop
 */
const filterFlightsStop = (
  flight: IFlightSearchResultsProposal,
  stop: EStopsFilterType
) => {
  switch (stop) {
    case EStopsFilterType.NON_STOP:
      return flight.max_stops === 0;
    case EStopsFilterType.ONE_STOP:
      return flight.max_stops <= 1;
    case EStopsFilterType.TWO_STOP:
      return flight.max_stops <= 2;
    default:
      return true;
  }
};

const filterFlightsDuration = (
  flight: IFlightSearchResultsProposal,
  minDuration: number,
  maxDuration: number
) => {
  if (!minDuration || !maxDuration) {
    return true;
  }

  return !!flight.segment_durations.filter(
    (duration) => duration >= minDuration && duration <= maxDuration
  ).length;
};

const filterFlightsPrice = (
  flight: IFlightSearchResultsProposal,
  minPrice: number,
  maxPrice: number
) => {
  if (!minPrice || !maxPrice) {
    return true;
  } else {
    const terms = Object.values(flight.terms);
    const minTermsPrice = minBy(terms, "unified_price");
    const maxTermsPrice = maxBy(terms, "unified_price");
    return (
      minTermsPrice.unified_price >= minPrice &&
      maxTermsPrice.unified_price <= maxPrice
    );
  }
};

