const filterFlightsAirline = (
  flight: IFlightSearchResultsProposal,
  airlines: any
) => {
  if (airlines.length) {
    // Airlines in array are selected: false
    const hasAirlineFromFilter = flight.carriers.find((airlineCode) =>
      airlines.find((notWantedAirline: any) => notWantedAirline === airlineCode)
    );

    if (hasAirlineFromFilter) {
      return false;
    } else {
      return true;
    }
  } else {
    return true;
  }
};

// TODO NOT READY
const filterFlightsGates = (
  flight: IFlightSearchResultsProposal,
  gates: any
) => {
  // make a list of all filtered agents and if its more than filter show
  // aka 3 agents total 5 agents in flight tickets = 2 unique so its ok to show
  if (gates.length) {
    let isValidGates = true;
    const flightTermsKeys = Object.keys(flight.terms);
    // if flight terms has term that is not filtered return true
    if (gates.length >= flightTermsKeys.length) {
      const hasUniqueTerm = flightTermsKeys.some((key) => {
        const value = flight.terms[key];
        // true if we have not found the term in filtered
        return !gates.find((filteredGate: any) => filteredGate === value);
      });

      if (!hasUniqueTerm) {
        isValidGates = false;
      }

      return isValidGates;
    } else {
      return true;
    }
  } else {
    return true;
  }
};
