import dynamic from "next/dynamic";

const En = dynamic(() => import("./en"));
const Ru = dynamic(() => import("./ru"));
const Es = dynamic(() => import("./es"));
const Cz = dynamic(() => import("./cz"));
const De = dynamic(() => import("./de"));
const Gr = dynamic(() => import("./gr"));
const Fr = dynamic(() => import("./fr"));
const Id = dynamic(() => import("./id"));
const It = dynamic(() => import("./it"));
const Pl = dynamic(() => import("./pl"));
const Pt = dynamic(() => import("./pt"));
const Vi = dynamic(() => import("./vi"));
const Th = dynamic(() => import("./th"));
const Kr = dynamic(() => import("./kr"));
const Ar = dynamic(() => import("./ar"));
const He = dynamic(() => import("./he"));
const Cn = dynamic(() => import("./cn"));
const Ro = dynamic(() => import("./ro"));

export const FlagByLocale = ({ locale, ...props }: any) => {
  switch (locale) {
    case "en":
      return <En {...props} />;
    case "cz":
    case "cs":
      return <Cz {...props} />;
    case "ru":
      return <Ru {...props} />;
    case "es":
      return <Es {...props} />;
    case "de":
      return <De {...props} />;
    case "gr":
    case "el":
      return <Gr {...props} />;
    case "fr":
      return <Fr {...props} />;
    case "id":
      return <Id {...props} />;
    case "pl":
      return <Pl {...props} />;
    case "pt":
      return <Pt {...props} />;
    case "vi":
    case "vn":
      return <Vi {...props} />;
    case "th":
      return <Th {...props} />;
    case "kr":
    case "ko":
      return <Kr {...props} />;
    case "it":
      return <It {...props} />;
    case "il":
    case "he":
      return <He {...props} />;
    case "ar":
      return <Ar {...props} />;
    case "cn":
    case "zh":
      return <Cn {...props} />;
    case "ro":
      return <Ro {...props} />;
    default:
      return <En {...props} />;
  }
};
