export const navigateToSearchResults = ({
  replaceUrl,
  searchState,
  push,
  replace,
  t,
  query
}: any) => {
  const {
    departureDate,
    returnDate,
    tripClass = { id: "Y" },
    adults = 1,
    children = 0,
    infants = 0,
    redirectToBooking,
    from,
    to,
  } = searchState;
  const departure = departureDate
    ? departureDate?.toISOString().split("T")[0]
    : null;
  const dateReturn = returnDate
    ? returnDate?.toISOString().split("T")[0]
    : "none";

  const queryParams = {
    tripClass: tripClass?.id,
    adults,
    children,
    infants,
    redirectToBooking,
  } as any;

  const routeParams = [
    t("routes-search"),
    `${from?.code}:${from?.city_name}:${from?.country_name}`,
    to?.country_name ? `${to?.code}:${to?.city_name}:${to?.country_name}` : `${to?.code}:${to?.city_name}`,
    departure,
  ];

  if (dateReturn) {
    routeParams.push(dateReturn);
  }

  const newRoutePath = "/" + routeParams.join("/");
  const newQueryPath = Object.keys(queryParams)
    .map((key) => key + "=" + queryParams[key])
    .join("&");

  const gclid = query?.gclid ? `&gclid=${query.gclid}` : '';
  const newUrl = `${newRoutePath}?${newQueryPath}${gclid}`;

  if (redirectToBooking && !replaceUrl) {
    window.open(newUrl);
    if (returnDate) {
      window.location.replace(
        `https://tp.media/r?marker=56123&trs=15329&p=2076&u=https%3A%2F%2Fwww.booking.com%2Fsearchresults.en-gb.html%3Fss%3D${to.city_name}%2C+${to.country_name}%26checkin%3D${departure}%26checkout%3D${dateReturn}`
      );
    } else {
      window.location.replace(
        `https://tp.media/r?marker=56123&trs=15329&p=2076&u=https%3A%2F%2Fwww.booking.com%2Fsearchresults.en-gb.html%3Fss%3D${to.city_name}%2C+${to.country_name}%26checkin%3D${departure}`
      );
    }
  } else {
    replaceUrl ? replace(newUrl) : push(newUrl);
  }
};
