import md5 from 'blueimp-md5';
import { ksort } from './ksort';

export const generateSignature = (
  flightSearchRequest: any,
  apiKey: string,
  additionalMarker?: string
) => {
  if (additionalMarker) {
    flightSearchRequest.marker = `${flightSearchRequest.marker}.${additionalMarker}`;
  }

  const sortedFlightSearchRequest: any = ksort(flightSearchRequest);

  sortedFlightSearchRequest.passengers = ksort(flightSearchRequest.passengers);

  flightSearchRequest.segments.forEach((segment: any, index: any) => {
    const sortedSegment = ksort(segment) as any;
    sortedFlightSearchRequest.segments[index] =
      Object.values(sortedSegment).join(":");
  });

  sortedFlightSearchRequest.passengers = Object.values(
    flightSearchRequest.passengers
  ).join(":");
  sortedFlightSearchRequest.segments = Object.values(
    sortedFlightSearchRequest.segments
  ).join(":");

  const signature = apiKey
    ? [apiKey].concat(Object.values(sortedFlightSearchRequest)).join(":")
    : Object.values(sortedFlightSearchRequest).join(":");

  return md5(signature);
};
